import React, { useState } from "react";
import "../App.css";
import Texto from "./Texto";
import { NoPrint } from "react-easy-print";

function Education(props) {
  const handleChange = (e, index) => {
    // it will update the value based on the index
    const { name, value } = e.target;
    const list = [...props.education];
    list[index][name] = value;
    props.setEducation(list);
  };

  const handleRemoveInput = (index) => {
    const list = [...props.education];
    list.splice(index, 1);
    props.setEducation(list);
  };

  const [editMode, setEditMode] = useState(true);

  const handleSubmit = (e) => {
    e.preventDefault();
    setEditMode((prevMode) => !prevMode);
  };

  if (!editMode) {
    return (
      <Texto
        schoolName={props.item.schoolName}
        title={props.item.title}
        from={props.item.from}
        to={props.item.to}
        handleEdit={handleSubmit}
      />
    );
  }
  return (
    <NoPrint>
      <form key={props.id} className="box" onSubmit={handleSubmit}>
        <input
          type="text"
          name="schoolName"
          placeholder="School Name"
          className="mr10"
          value={props.item.schoolName}
          onChange={(e) => handleChange(e, props.id)} // tenemos que usar un segundo atributo (index) para manejar multipls values.
          required
        />

        <input
          type="text"
          name="title"
          placeholder="Title of study"
          className="mr10"
          value={props.item.title}
          onChange={(e) => handleChange(e, props.id)}
          required
        />

        <input
          type="date"
          name="from"
          placeholder="From"
          className="mr10"
          value={props.item.from}
          onChange={(e) => handleChange(e, props.id)}
          required
        />

        <input
          type="date"
          name="to"
          placeholder="To"
          className="mr10"
          value={props.item.to}
          onChange={(e) => handleChange(e, props.id)}
          required
        />

        <button className="formBtn">Save</button>

        {props.education.length > 1 && (
          <input
            type="button"
            value="remove"
            className="mr10"
            onClick={() => handleRemoveInput(props.id)}
          />
        )}

        {/*       {props.education.length - 1 === props.id && <input
          type="button"
          value = "add"
          onClick={props.handleAddInput}
          />} */}
      </form>
    </NoPrint>
  );
}

export default Education;
