import React, { useState } from "react";
import "../App.css";
import Texto from "./Texto";
import { NoPrint } from "react-easy-print";

function Personal(props) {
  const handleChange = (e, index) => {
    // it will update the value based on the index
    const { name, value } = e.target;
    const list = [...props.personal];
    list[index][name] = value;
    props.setPersonal(list);
  };

  const [editMode, setEditMode] = useState(true);

  const handleSubmit = (e) => {
    e.preventDefault();
    setEditMode((prevMode) => !prevMode);
  };

  if (!editMode) {
    return (
      <Texto
        firstName={props.item.firstName}
        lastName={props.item.lastName}
        email={props.item.email}
        phoneNumber={props.item.phoneNumber}
        handleEdit={handleSubmit}
      />
    );
  }
  return (
    <NoPrint>
      <form key={props.id} className="box" onSubmit={handleSubmit}>
        <input
          type="text"
          name="firstName"
          placeholder="First Name"
          className="mr10"
          value={props.item.firstName}
          onChange={(e) => handleChange(e, props.id)} // tenemos que usar un segundo atributo (index) para manejar multipls values.
          required
        />

        <input
          type="text"
          name="lastName"
          placeholder="Last Name"
          className="mr10"
          value={props.item.lastName}
          onChange={(e) => handleChange(e, props.id)}
          required
        />

        <input
          type="email"
          name="email"
          placeholder="example@gmail.com"
          className="mr10"
          value={props.item.email}
          onChange={(e) => handleChange(e, props.id)}
          required
        />

        <input
          type="text"
          name="phoneNumber"
          placeholder="Phone Number"
          className="mr10"
          value={props.item.phoneNumber}
          onChange={(e) => handleChange(e, props.id)}
          required
        />

        <button className="formBtn">Save</button>

        {/*       {props.personal.length - 1 === props.id && <input
          type="button"
          value = "add"
          onClick={props.handleAddInput}
          />} */}
      </form>
    </NoPrint>
  );
}

export default Personal;
