import React, { useState } from "react"
import "./App.css"
import Personal from "./components/Personal"
import Education from "./components/Education"
import Experience from "./components/Experience"
import PrintProvider, { NoPrint, Print } from "react-easy-print"
import Footer from "./components/Footer"
import Header from "./components/Header"
import { ThemeProvider } from "styled-components"
import Theme from "./theme/Theme"
/* import GlobalStyle from "./theme/GlobalStyle" */

function App() {
  const [personal, setPersonal] = useState([
    { firstName: "", lastName: "", email: "", phoneNumber: "" },
  ])
  const [education, setEducation] = useState([
    { schoolName: "", title: "", from: "", to: "" },
  ])
  const [experience, setExperience] = useState([
    { company: "", position: "", from: "", to: "", tasks: "" },
  ])

  const handleAddInput = (type) => {
    if (type === "education") {
      setEducation([
        ...education,
        { schoolName: "", title: "", from: "", to: "" },
      ])
    } else
      setExperience([
        ...experience,
        { company: "", position: "", from: "", to: "", tasks: "" },
      ])
  }

  return (
    <ThemeProvider theme={Theme}>
      {/* <GlobalStyle /> */}
      <PrintProvider>
        <div className="App">
          <NoPrint>
            <Header />
          </NoPrint>
          <div className="sheet">
            <NoPrint>
              <h1>Create your CV for Free!</h1>
            </NoPrint>

            <Print>
              <h2>Personal Information</h2>
            </Print>

            {personal.map((e, index) => (
              <Personal
                key={index}
                item={e}
                id={index}
                personal={personal}
                setPersonal={setPersonal}
              />
            ))}
            <Print>
              <h2>Education</h2>
            </Print>

            {education.map((e, index) => (
              <Education
                key={index}
                item={e}
                id={index}
                education={education}
                setEducation={setEducation}
              />
            ))}
            <NoPrint>
              <button value="add" onClick={() => handleAddInput("education")}>
                Add
              </button>
            </NoPrint>

            <Print>
              <h2>Experience</h2>
            </Print>

            {experience.map((e, index) => (
              <Experience
                key={index}
                item={e}
                id={index}
                experience={experience}
                setExperience={setExperience}
              />
            ))}
            <NoPrint>
              <button value="add" onClick={handleAddInput}>
                Add
              </button>
            </NoPrint>

            <NoPrint>
              <button
                onClick={() => {
                  window.print()
                }}
                className="print"
              >
                Print
              </button>
            </NoPrint>
            {/*   <pre>
      {JSON.stringify(personal, null, 2)}
    </pre> */}
          </div>
          <Footer />
        </div>
      </PrintProvider>
    </ThemeProvider>
  )
}

export default App
