import React, { useState } from "react";
import "../App.css";
import Texto from "./Texto";
import { NoPrint } from "react-easy-print";

function Experience(props) {
  const handleChange = (e, index) => {
    // it will update the value based on the index
    const { name, value } = e.target;
    const list = [...props.experience];
    list[index][name] = value;
    props.setExperience(list);
  };

  const handleRemoveInput = (index) => {
    const list = [...props.experience];
    list.splice(index, 1);
    props.setExperience(list);
  };

  const [editMode, setEditMode] = useState(true);

  const handleSubmit = (e) => {
    e.preventDefault();
    setEditMode((prevMode) => !prevMode);
  };

  if (!editMode) {
    return (
      <Texto
        company={props.item.company}
        position={props.item.position}
        from={props.item.from}
        to={props.item.to}
        tasks={props.item.tasks}
        handleEdit={handleSubmit}
      />
    );
  }
  return (
    <NoPrint>
      <form key={props.id} className="box" onSubmit={handleSubmit}>
        <input
          type="text"
          name="company"
          placeholder="Company"
          className="mr10"
          value={props.item.company}
          onChange={(e) => handleChange(e, props.id)} // tenemos que usar un segundo atributo (index) para manejar multipls values.
          required
        />

        <input
          type="text"
          name="position"
          placeholder="Position"
          className="mr10"
          value={props.item.position}
          onChange={(e) => handleChange(e, props.id)}
          required
        />

        <input
          type="date"
          name="from"
          placeholder="From"
          className="mr10"
          value={props.item.from}
          onChange={(e) => handleChange(e, props.id)}
          required
        />

        <input
          type="date"
          name="to"
          placeholder="To"
          className="mr10"
          value={props.item.to}
          onChange={(e) => handleChange(e, props.id)}
          required
        />

        <input
          type="text"
          name="tasks"
          placeholder="Tasks"
          className="mr10"
          value={props.item.tasks}
          onChange={(e) => handleChange(e, props.id)}
          required
        />

        <button className="formBtn">Save</button>

        {props.experience.length > 1 && (
          <input
            type="button"
            value="remove"
            className="mr10"
            onClick={() => handleRemoveInput(props.id)}
          />
        )}

        {/*       {props.experience.length - 1 === props.id && <input
          type="button"
          value = "add"
          onClick={props.handleAddInput}
          />} */}
      </form>
    </NoPrint>
  );
}

export default Experience;
